import React, { Component } from "react";
import Page from "./components/Page/";
import "./App.css";

class App extends Component {

	render() {
		return (
			<div className="App">
				<Page />
				<br />
				<a href="/auth/spotify">Spotify Login</a>
			</div>
		);
	}
}

export default App;



